.study

    &__breadcrumbs
        font-size: 18px
        color: $gray-color
        margin-bottom: 40px

    &__title
        text-align: center
        margin-bottom: 60px

    &__body
        display: grid
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr))
        justify-content: center
        gap: 30px

    &__item
        border-radius: 10px
        display: flex
        flex-direction: column

    &__bottom
        display: flex
        justify-content: center
        padding: 50px 0 20px 0

    &__btn
        height: 45px
        padding: 5px 35px

.item-study

    &__body

    &__image
        border-top-left-radius: 10px
        border-top-right-radius: 10px
        margin-bottom: 15px

        img
            border-top-left-radius: 10px
            border-top-right-radius: 10px
            width: 100%
            height: 100%
            object-fit: cover

    &__content
        display: flex
        flex-direction: column
        flex: 1 1 auto

    &__title
        font-size: 20px
        font-weight: 700
        margin-bottom: 15px

    &__descr
        flex: 1 1 auto
