.registration

    &__breadcrumbs
        font-size: 18px
        color: $gray-color
        margin-bottom: 40px

    &__title
        font-weight: 700
        font-size: 42px
        color: $gray-color
        margin-bottom: 20px

    &__body
        display: flex
        align-items: flex-start

    &__tabs
        flex: 1 1 20%
        padding-right: 100px

    &__form
        padding: 20px
        flex: 1 1 70%
        background-color: $light-gray-color

.tabs-registration

    &__tab

    &__btn
        display: inline-flex
        width: 100%
        align-items: center
        justify-content: flex-start
        padding: 10px
        border-bottom: 1px solid $gray-color
        transition: color 0.3s ease, border-bottom 0.3s ease
        &.active
            color: $pink-color
            border-bottom: 1px solid $pink-color

.form-registration

    &__form

        &_individual

        &_company

    &__body
        display: flex
        align-items: flex-start

    &__column
        flex: 1 1 50%

    &__label
        display: block
        margin-bottom: 10px
        width: 300px

        &_checkbox

    &__name
        display: block
        padding-left: 10px
        margin-bottom: 3px
        width: 100%
        span
            color: $pink-color

    &__input
        font-family: 'PFDinTextCompPro'
        display: block
        border: 1px solid $gray-color
        border-radius: 20px
        width: 100%
        height: 32px
        padding: 0 10px
        outline: none
        transition: border 0.3s ease, box-shadow 0.3s ease
        &:focus
            border: none
            box-shadow: 0 0 2px 2px rgba(252, 45, 183, 0.4)
        &::placeholder
            color: $gray-color
            font-size: 14px
            font-family: 'PFDinTextCompPro'

    &__checkbox

    &__tag
        padding-left: 35px
        &::before
            top: 1px
            left: 10px
        &::after
            top: 4px
            left: 13px
        a
            color: $pink-color
            transition: color 0.3s ease 0s
            @media (any-hover: hover)
                &:hover
                    color: $hover-pink-color

    &__add
        margin-top: 30px

    &__text
        padding-left: 10px
        span
            color: $pink-color
