}

.product-item {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 10px 5px rgba(194, 63, 119, 0.4);
  display: flex;
  flex-direction: column;
}
.product-item__image {
  padding: 5px;
  width: 100%;
  margin-bottom: 10px;
}
.product-item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.product-item__content {
  padding: 10px;
  color: #654f5e;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.product-item__title {
  display: block;
  flex: 1 1 auto;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #654f5e;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .product-item__title:hover {
    color: #fc2db7;
  }
}
.product-item__options {
  margin-bottom: 10px;
}
.product-item__weight-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #654f5e;
  border-radius: 20px;
  font-weight: 500;
  color: #654f5e;
  height: 25px;
  padding: 5px 10px;
  font-family: "PFDinTextCompPro";
  transition: color 0.3s ease, background-color 0.3s ease;
  margin: 2px;
}
.product-item__weight-btn.active {
  color: #ffffff;
  background-color: #654f5e;
}
.product-item__prices {
  margin-bottom: 10px;
  padding-left: 5px;
}
.product-item__price {
  margin-right: 10px;
  font-weight: 700;
  font-size: 20px;
}
.product-item__price span {
  font-size: 16px;
}
.product-item__price-new {
  font-weight: 700;
  font-size: 20px;
}
.product-item__price-new span {
  font-size: 16px;
}
.product-item__price-old {
  text-decoration: line-through;
  font-weight: 700;
  font-size: 20px;
}
.product-item__price-old span {
  font-size: 16px;
}
.product-item__bottom {
  display: grid;
  grid-template-columns: 1.5fr 1.5fr 3fr;
  gap: 10px;
  width: 100%;
}

.item-bottom__label {
  display: block;
  width: 100%;
  border: 1px solid #654f5e;
}
.item-bottom__input {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
}
.item-bottom__btns {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.item-bottom__btn {
  border-radius: 10px;
}
.item-bottom__btn_plus {
  margin-bottom: 5px;
}
.item-bottom__button {
  border-radius: 10px;
  padding: 5px;
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox:focus-visible + .checkbox-tag {
  outline: 2px solid #654f5e;
  outline-offset: 3px;
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #654f5e;
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 1px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: #654f5e;
  transform: scale(0);
}

.show {
  display: block;
}

.hide {
  display: none;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
.quantity__btn {
  flex: 0 0 25px;
  height: 25px;
  border-radius: 100%;
}
.quantity__btn.disabled {
  pointer-events: none;
  background-color: #ebedee;
}
.quantity__current-number {
  flex: 0 0 45px;
  height: 30px;
  border: 2px solid #654f5e;
  margin: 0 10px;
}
.quantity__input {
  width: 100%;
  height: 100%;
  outline: none;
  padding: 0 5px;
  text-align: center;
  font-weight: 700;
  color: #654f5e;
}
.quantity input::-webkit-outer-spin-button,
.quantity input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  z-index: 20000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
  opacity: 1;
  visibility: visible;
}
.preloader.done {
  opacity: 0;
  visibility: hidden;
}

