  letter-spacing: 1px;
  padding: 10px;
  text-align: center;
}
.header__logo span {
  display: block;
  font-weight: 400;
  margin-top: -5px;
  font-size: 30px;
  letter-spacing: 3px;
}
.header__content {
  flex: 1 1 auto;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
}
.header__main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 0;
}
.header__bottom {
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-header__phones {
  display: flex;
  align-items: center;
}
