.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  background-color: rgba(21, 21, 21, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.1s ease, visibility 0.1s ease;
}
.modal.active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.modal__close {
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  color: #ffffff;
  font-size: 26px;
  background-color: transparent;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .modal__close:hover {
    color: #fc2db7;
  }
}
.modal__content {
  border-radius: 10px;
  width: 360px;
  padding: 30px;
  background-color: #ffffff;
}
.modal__form_login, .modal__form_callback, .modal__form_study {
  display: none;
}
.modal__form_login.active, .modal__form_callback.active, .modal__form_study.active {
  display: block;
}
.modal__form_callback .form-modal__btns, .modal__form_study .form-modal__btns {
  justify-content: center;
}

